:root {
  --scrollbar-y: linear-gradient(to right, #e5e5e5, var(--surface) 20%);
  --scrollbar-x: linear-gradient(to bottom, #e5e5e5, var(--surface) 20%);
}

body {
  font-family: Arial;
  font-size: 12px;
  color: #222222;
}

.surface {
  background: var(--surface);
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

u {
  text-decoration: none;
  border-bottom: 0.5px solid #222222;
}

code {
  &,
  * {
    font-family: monospace;
  }
}

pre {
  display: block;
  margin: 0;
  padding: 12px 8px;
  background: #fff;
  border: 1px solid var(--button-border-color);
}

summary {
  &:focus {
    outline: 1px dotted #000000;
  }
}

::-webkit-scrollbar {
  width: 16px;

  &:horizontal {
    height: 17px;
  }

  &-corner {
    background: var(--button-face);
  }
  
  &-track {
    &:vertical {
      background: var(--scrollbar-y);
    }
  
    &:horizontal {
      background: var(--scrollbar-x);
    }
  }

  &-thumb {
    border: var(--button-border);
    border-color: var(--button-border-color);
    border-radius: var(--border-radius);
    box-shadow: var(--button-shadow);
    background-color: var(--button-face);
  
    &:vertical {
      background: url("data:image/png;base64,")
          no-repeat center,
        linear-gradient(
          to right,
          var(--button-face) 45%,
          var(--button-shade-light) 45%,
          var(--button-shade-dark)
        );
    }
  
    &:horizontal {
      background: url("data:image/png;base64,")
          no-repeat center,
        linear-gradient(
          to bottom,
          var(--button-face) 45%,
          var(--button-shade-light) 45%,
          var(--button-shade-dark)
        );
    }

    &:hover {
      &:vertical {
        border-color: var(--button-border-color-hovered);
        background: url("data:image/png;base64,")
            no-repeat center,
          linear-gradient(
            to right,
            var(--button-face-hover) 45%,
            var(--button-shade-light-hovered) 45%
          );
      }
  
      &:horizontal {
        border-color: var(--button-border-color-hovered);
        background: url("data:image/png;base64,")
            no-repeat center,
          linear-gradient(
            to bottom,
            var(--button-face-hover) 45%,
            var(--button-shade-light-hovered) 45%
          );
      }
    }

    &:active {
      &:vertical {
        border-color: var(--button-border-color-active);
        background: url("data:image/png;base64,")
            no-repeat center,
          linear-gradient(
            to right,
            var(--button-face-active) 45%,
            var(--button-shade-light-active) 45%
          );
      }
  
      &:horizontal {
        border-color: var(--button-border-color-active);
        background: url("data:image/png;base64,")
            no-repeat center,
          linear-gradient(
            to bottom,
            var(--button-face-active) 45%,
            var(--button-shade-light-active) 45%
          );
      }
    }
  }
  
  &-button:horizontal:start:decrement,
  &-button:horizontal:end:increment,
  &-button:vertical:start:decrement,
  &-button:vertical:end:increment {
    display: block;
  }

  &-button {
    /* Add an invisible border to prevent shifting
       when hovering the scrollbar buttons */
    border: var(--button-border);
    border-color: #0000;

    &:vertical {
      height: 17px;

      &:start {
        background: url("./icon/button-up.svg"), var(--scrollbar-y);
      }

      &:end {
        background: url("./icon/button-down.svg"), var(--scrollbar-y);
      }
    }

    &:horizontal {
      width: 16px;

      &:start {
        background: url("./icon/button-left.svg"), var(--scrollbar-x);
      }

      &:end {
        background: url("./icon/button-right.svg"), var(--scrollbar-x);
      }
    }

    &:hover {
      border: var(--button-border);
      border-color: var(--button-border-color);
      border-radius: var(--border-radius);
      box-shadow: var(--button-shadow);
      background-color: var(--button-face);
      
      &:vertical {
        height: 17px;
  
        &:start {
          border-color: var(--button-border-color-hovered);
          background: url("./icon/button-up.svg"),
                      linear-gradient(
                        to right,
                        var(--button-face-hover) 45%,
                        var(--button-shade-light-hovered) 45%
                      );
        }
  
        &:end {
          border-color: var(--button-border-color-hovered);
          background: url("./icon/button-down.svg"),
                      linear-gradient(
                        to right,
                        var(--button-face-hover) 45%,
                        var(--button-shade-light-hovered) 45%
                      );
        }
      }
  
      &:horizontal {
        width: 16px;
  
        &:start {
          border-color: var(--button-border-color-hovered);
          background: url("./icon/button-left.svg"),
                      linear-gradient(
                        to bottom,
                        var(--button-face-hover) 45%,
                        var(--button-shade-light-hovered) 45%
                      );
        }
  
        &:end {
          border-color: var(--button-border-color-hovered);
          background: url("./icon/button-right.svg"),
                      linear-gradient(
                        to bottom,
                        var(--button-face-hover) 45%,
                        var(--button-shade-light-hovered) 45%
                      );
        }
      }
    }

    &:active {
      border: var(--button-border);
      border-color: var(--button-border-color);
      border-radius: var(--border-radius);
      box-shadow: var(--button-shadow);
      background-color: var(--button-face);
      
      &:vertical {
        height: 17px;
  
        &:start {
          border-color: var(--button-border-color-active);
          background: url("./icon/button-up.svg"),
                      linear-gradient(
                        to right,
                        var(--button-face-active) 45%,
                        var(--button-shade-light-active) 45%
                      );
        }
  
        &:end {
          border-color: var(--button-border-color-active);
          background: url("./icon/button-down.svg"),
                      linear-gradient(
                        to right,
                        var(--button-face-active) 45%,
                        var(--button-shade-light-active) 45%
                      );
        }
      }
  
      &:horizontal {
        width: 16px;
  
        &:start {
          border-color: var(--button-border-color-active);
          background: url("./icon/button-left.svg"),
                      linear-gradient(
                        to bottom,
                        var(--button-face-active) 45%,
                        var(--button-shade-light-active) 45%
                      );
        }
  
        &:end {
          border-color: var(--button-border-color-active);
          background: url("./icon/button-right.svg"),
                      linear-gradient(
                        to bottom,
                        var(--button-face-active) 45%,
                        var(--button-shade-light-active) 45%
                      );
        }
      }
    }
  }
}
